html body {
  margin: 0;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(34, 68, 171, 1) 0%,
    rgba(37, 72, 174, 1) 28%,
    rgba(39, 132, 224, 1) 100%,
    rgba(39, 125, 218, 1) 100%
  );
}
.App {
  font-family: sans-serif;
  height: 94vh;
  width: 96vw;
  margin-top: 3vh;
  margin-left: 2vw;
  display: grid;
  grid-template: 0.15fr 1fr 1fr / 1fr 4fr;
  box-sizing: border-box;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(34, 68, 171, 1) 0%,
    rgba(37, 72, 174, 1) 28%,
    rgba(39, 132, 224, 1) 100%,
    rgba(39, 125, 218, 1) 100%
  );
}

.nav {
  border-radius: 1rem 1rem 0 0;
  background-color: white;
  grid-column: 1 / 3;
  border-bottom: 2px solid #eeeff1;
  text-align: center;
  color: rgb(53, 49, 49);
}
.nav h3 {
  margin: 1.6% 4%;
  letter-spacing: 0.2rem;
  font-size: 1.3rem;
}
.panel {
  background-color: #f9fafc;
  grid-row: 2 / 4;
  border-radius: 0 0 0 1rem;
  border-right: 2px solid #eeeff1;
}

.editor {
  position: relative;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  background-color: white;
  text-align: left;
  border-bottom: 2px solid #eeeff1;
}

.result {
  background-color: #fdfdfd;
  grid-column: 2 / 3;
  grid-row: 3 / 4;
  border-radius: 0 0 1rem 0;
  border-top: 2px solid #eeeff1;
}

div.CodeMirror-gutters {
  border-right: 1px solid white;
  background-color: white;
}

.run {
  position: absolute;
  left: 3%;
  bottom: 4%;
  height: 5vh;
  width: 8vw;
  border-radius: 0.3rem;
  border: 0;
  background-color: rgb(82, 194, 123);
  color: white;
  font-size: 1.2vw;
}

/* result css */
.table-bordered thead {
  border-radius: 0.5rem;
  color: grey;
}

.result th {
  padding-left: 3% !important;
  width: 20%;
}

.result td {
  padding-left: 3% !important;
  width: auto;
}

.resultheader {
  width: 100%;
  height: 14%;
  text-align: center;
  padding: 1% 10% 1% 0;
  font-size: 1.2rem;
  letter-spacing: 0.05rem;
}

/* panel css */

.panelHeader {
  text-align: center;
  margin: 15% 10%;
  font-size: 1.5vw;
  font-weight: 600;
  color: rgb(83, 83, 83);
  letter-spacing: 0.2vw;
}

.dbTable {
  border-top: 2px solid #eeeff1;
  padding: 10% 0 0 25% !important;
  text-align: left;
}
.panel td,
th {
  text-align: left;
  padding-top: 8%;
  font-size: 1.1vw;
  letter-spacing: 0.15vw;
}

.panel th {
  font-size: 1.2vw;
}

.savedQuery {
  margin-top: 12%;
  border-top: 2px solid #eeeff1;
  text-align: left;
  padding: 13% 22%;
  font-size: 1.2vw;
  font-weight: 600;
  letter-spacing: 0.08vw;
}

.savedQuery p {
  font-weight: 500;
  margin: 6%;
  font-size: 1.1vw;
}
